import React from 'react'
import styled from 'styled-components'
import { faCheck, faTimes } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import colors from '../color-css/color'

export type Props = {
  isCorrect: boolean;
  message: string;
  visibility: boolean;
}

interface Container {
  visibility: boolean
}
const Container = styled.div<Container>`
  max-width: auto;
  display: ${props => props.visibility === true ? 'block' : 'none'};
`

interface ValidationIndicatorIconWrapper {
  correct: boolean
}

const ValidationIndicatorIconWrapper = styled.div<ValidationIndicatorIconWrapper>`
  width: 1.37rem;
  height: 1.37rem;
  border-radius: 50%;
  background-color: ${props => props.correct === true ? colors.green : colors.red};
  display: inline-block;
  vertical-align: middle;
  animation-name: largeToSmall;
  animation-duration: .75s;
  animation-timing-function: ease-in-out;

  @keyframes largeToSmall {
  0% {
    transform: scale(1, 1);
    }
  25% {
    transform: scale(1, 1);
    }
  50% {
    transform: scale(1.1, 1.1);
    }
  75% {
    transform: scale(1, 1);
    }
  100% {
    transform: scale(1, 1);
    }
`

const StyledCheckIcon = styled(FontAwesomeIcon)
  `
  color: #fff;
  width: 0.6875em;
  height: 0.6875em;
  text-align: center;
`

const CenteringDiv = styled.div`
  width: 1.365rem;
  height: 1.365rem;
  display: flex;
  align-items: center;
  justify-content: center;
  animation-name: largeToSmall;
  animation-duration: .75s;
  animation-timing-function: ease-in-out;
  @keyframes largeToSmall {
  0% {
    transform: scale(1, 1);
    }
  25% {
    transform: scale(1, 1);
    }
  50% {
    transform: scale(1.1, 1.1);
    }
  75% {
    transform: scale(1, 1);
    }
  100% {
    transform: scale(1, 1);
    }
`

interface ValidationIndicatorMessage {
  correct: boolean
}
const ValidationIndicatorMessage = styled.span<ValidationIndicatorMessage>`
  font-family: museo-sans, sans-serif;
  color: ${props => props.correct === true ? colors.green : colors.red};
  font-size: 1rem;
  font-style: Italic;
  font-weight: 500;
  text-color: ${props => props.correct === true ? colors.green : colors.red};
  margin-left: 0.9375rem;
  animation-name: fadeIn;  
  animation-duration: 1s;
  
  animation-timing-function: linear;

  @keyframes fadeIn {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`

export const ValidationIndicator = (props: any) => {
  if (props.isCorrect === true) {
    return (
      <Container visibility={props.visibility}>
        <ValidationIndicatorIconWrapper correct={props.isCorrect}>
          <CenteringDiv>
            <StyledCheckIcon icon={faCheck} />
          </CenteringDiv>
        </ValidationIndicatorIconWrapper>
        <ValidationIndicatorMessage correct={props.isCorrect}> {props.message} </ValidationIndicatorMessage>
      </Container >);
  }

  return (
    <Container visibility={props.visibility}>
      <ValidationIndicatorIconWrapper correct={props.isCorrect}>
        <CenteringDiv>
          <StyledCheckIcon icon={faTimes} />
        </CenteringDiv>
      </ValidationIndicatorIconWrapper>
      <ValidationIndicatorMessage correct={props.isCorrect}> {props.message} </ValidationIndicatorMessage>
    </Container>);
}

export default ValidationIndicator;
